import { Link } from "react-router-dom";


export default function Footer() {


    return (

        <footer class="bg-blue-500 rounded-lg shadow dark:bg-gray-900 m-4 text-white">
            <div class="w-full max-w-screen-xl mx-auto p-4 md:py-8 ">
                <div class="sm:flex sm:items-center sm:justify-between">
                    <a href="https://flowbite.com/" class="flex items-center mb-4 sm:mb-0">

                        <span class="self-center text-2xl font-semibold whitespace-nowrap dark:text-white ">Denturo</span>
                    </a>
                    <ul class="flex flex-wrap items-center mb-6 text-sm font-mediumsm:mb-0 dark:text-gray-400">
                        <Link to={"/"}>
                            <a href="" class="mr-4 hover:underline md:mr-6 ">Home</a>
                        </Link>
                        <Link to={"/service"}>
                            <span class="mr-4 hover:underline md:mr-6">Service</span>
                        </Link>
                        <Link to={"/aboutus"}>
                            <span class="mr-4 hover:underline md:mr-6 ">About Us</span>
                        </Link>
                        <Link to={"/contactus"}>
                            <span class="hover:underline">Contact Us</span>
                        </Link>
                    </ul>
                </div>
                <hr class="my-6 border-gray-200 sm:mx-auto dark:border-gray-700 lg:my-8" />
                <span class="block text-sm  sm:text-center dark:text-gray-400 "> 2023 <span class="hover:underline">Denturo</span>. All Rights Reserved.</span>
            </div>
        </footer>


    );
}